import { makeAutoObservable } from "mobx";

class PopupActionsStore {
  currentAction = "";

  constructor() {
    makeAutoObservable(this);
  }

  setActions = (action = '') => {
    // action   (    ) -   action Modal
    this.currentAction = action;
  };
}

const popupActionsStore = new PopupActionsStore();
export default popupActionsStore;
