import {
  getCurrentDate,
  dateFormatter,
  calculateTotalPrice
} from "./OrderTools";

const shoppingCartMap = products => products.map(item => {
  //   '   
  const productMapping = { product_id: item.id, count: item.count };

  // ,   mods    
  if (Array.isArray(item.mods) && item.mods.length > 0) {
    //   mods,     m  a
    const modification = item.mods.map(mod => ({ m: mod.m, a: mod.a }));
    //    mods  
    productMapping.modification = modification;
  }

  return productMapping;
});


const shoppingCartMapPromo = products => products.map((item) => {
  //  '     involved_products
  const productMapping = { id: item.id, count: item.count };

  //       mods
  if (Array.isArray(item.mods) && item.mods.length > 0) {
    //  modification    m  a
    const modification = item.mods.map(mod => ({ m: mod.m, a: mod.a }));
    //  modification  ' 
    productMapping.modification = modification;
  }

  return {
    id: "2", //id   -40%
    involved_products: [productMapping]
  };
});

export const getValidateRules = (formData, cartItems, totalPrice, deliveryPrice) => {
  const { number, howToReciveOrder, houseNumber, street, deliveryTime, apartment, entrance, paymentMethod, change } = formData
  return [
    { check: () => cartItems.length === 0, message: ' ,    ' },
    { check: () => number === '', message: ' ,    ' },
    { check: () => !howToReciveOrder, message: ' ,    ' },
    {
      check: () => howToReciveOrder !== '' && (!houseNumber || !street),
      message: ' ,  '
    },
    {
      check: () => howToReciveOrder === ' ' && (!apartment || !entrance),
      message: " ,     "
    },
    {
      check: () => paymentMethod === '' && (+change < (totalPrice + deliveryPrice)),
      message: " ,      "
    },
    { check: () => !deliveryTime, message: ' ,    ' },
    { check: () => totalPrice <= 200, message: '   200 ' },
  ]
};


export const getOrderData = (formData, products, isPromotion) => {
  const {
    name,
    number,
    street,
    houseNumber,
    entrance,
    apartment,
    buildingCode,
    floor,
    howToReciveOrder,
    deliveryTime,
    selectedTime,
    withoutDevices,
    personCount,
    comment,
    paymentMethod,
    NotCall
  } = formData;

  const address1 = `: ${street} , : ${houseNumber}`;
  const address2 = howToReciveOrder === " " ? `: ${entrance}, : ${apartment}, : ${buildingCode}, : ${floor}` : " "
  const isAddressComment = howToReciveOrder === "  ";
  const serviceMode = howToReciveOrder === "" ? 2 : 3;
  const delivery_time = deliveryTime === " " ? getCurrentDate() : dateFormatter(selectedTime);
  const devicesComment = withoutDevices ? ",  " : "";
  const callOrNot = NotCall ? ",  " : "";
  const orderRecive = howToReciveOrder === 2 ? ", " : ""
  const isProm = isPromotion ? "  40%" : ""
  const com = ` : ${personCount},${devicesComment}${callOrNot}${orderRecive}${isProm},   : ${comment}`

  return {
    spot_id: 1,
    first_name: name,
    phone: number,
    products: shoppingCartMap(products),
    client_address: {
      address1: address1,
      address2: address2,
      comment: isAddressComment ? "  " : ""
    },
    payment: {
      type: paymentMethod === "" ? 0 : 1,
      sum: isPromotion ? calculateTotalPrice(products) * (60 / 100) : calculateTotalPrice(products),
      currency: "UAH",
    },
    service_mode: serviceMode,
    delivery_time: delivery_time,
    promotion: isPromotion ? shoppingCartMapPromo(products) : "",
    comment: com,
  };
}